/* You can add global styles to this file, and also import other style files */
:root {
  --main-color: #f7913c;
  --primary-color: #377FAE;
}

* {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}

html,
body {
  height: 100%;
}

body {
  margin: 0;
  font-family: Roboto, "Helvetica Neue", sans-serif;
}

.center-box {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.p-button.p-button-warning,
.p-buttonset.p-button-warning > .p-button,
.p-splitbutton.p-button-warning > .p-button,
.btn-outline-warning:hover,
.p-button.p-button-warning:enabled:hover,
.p-buttonset.p-button-warning > .p-button:enabled:hover,
.p-splitbutton.p-button-warning > .p-button:enabled:hover {
  color: #ffffff;
}

.p-button.p-button-warning,
.p-buttonset.p-button-warning > .p-button,
.p-splitbutton.p-button-warning > .p-button {
  border: 1px solid var(--main-color);
}

.mat-badge-content {
  color: var(--primary-color);
  background: var(--main-color);
}

.progress-bar {
  background-color: var(--primary-color) !important;
}

.ngx-file-drop__drop-zone {
  border: 5px dotted var(--main-color) !important;
  border-radius: 30px;
  height: 220px !important;
  margin: auto;
}

.ngx-file-drop__content {
  align-items: center;
  color: var(--primary-color) !important;
  display: flex;
  height: 220px !important;
  justify-content: center;
}

.btn-outline-warning,
.btn-outline-warning:hover,
.mat-radio-button.mat-accent.mat-radio-checked .mat-radio-outer-circle {
  border-color: var(--main-color);
}

.p-button:focus {
  outline: 0 none;
  outline-offset: 0;
  box-shadow: 0 0 0 0.2rem rgba(247, 145, 60, .5);
}

.btn-outline-warning,
a,
mat-checkbox,
.mat-form-field-label,
.mat-form-field-empty.mat-form-field-label,
.p-button.p-button-secondary.p-button-outlined,
.p-buttonset.p-button-secondary > .p-button.p-button-outlined,
.p-splitbutton.p-button-secondary > .p-button.p-button-outlined,
.mat-list-item.active,
.mat-button.mat-warn,
.mat-icon-button.mat-warn,
.mat-stroked-button.mat-warn,
.mat-flat-button.mat-warn,
.mat-raised-button.mat-warn,
.mat-fab.mat-warn,
.mat-mini-fab.mat-warn,
.nav-link,
.nav-pills .nav-link.active,
.nav-pills .show > .nav-link,
.float-action-button, .mat-button{
  color: var(--primary-color) !important;
}

.mat-checkbox-checked.mat-accent .mat-checkbox-ripple .mat-ripple-element {
  opacity: 0.03 !important;
}

.mat-checkbox-checked.mat-accent .mat-checkbox-background,
.mat-checkbox-indeterminate.mat-accent .mat-checkbox-background,
.mat-radio-button.mat-accent .mat-radio-inner-circle,
.mat-radio-button.mat-accent .mat-radio-ripple .mat-ripple-element:not(.mat-radio-persistent-ripple),
.mat-radio-button.mat-accent.mat-radio-checked .mat-radio-persistent-ripple,
.mat-radio-button.mat-accent:active .mat-radio-persistent-ripple,
.mat-checkbox-checked.mat-accent .mat-checkbox-ripple .mat-ripple-element,
.mat-focused .mat-form-field-underline .mat-form-field-ripple,
.mat-form-field-underline,
.mat-step-header .mat-step-icon-selected,
.mat-step-header .mat-step-icon-state-done,
.mat-step-header .mat-step-icon-state-edit,
.btn-outline-warning:hover,
.p-button.p-button-warning,
.p-buttonset.p-button-warning > .p-button,
.p-splitbutton.p-button-warning > .p-button,
.p-button.p-button-warning:enabled:hover,
.p-buttonset.p-button-warning > .p-button:enabled:hover,
.p-splitbutton.p-button-warning > .p-button:enabled:hover,
.main-color-btn {
  background-color: var(--main-color) !important;
}

.p-button.p-button-secondary.p-button-outlined,
.p-buttonset.p-button-secondary > .p-button.p-button-outlined,
.p-splitbutton.p-button-secondary > .p-button.p-button-outlined {
  background-color: transparent;
  border: 1px solid;
}

.p-button.p-button-secondary:enabled:focus,
.p-buttonset.p-button-secondary > .p-button:enabled:focus,
.p-splitbutton.p-button-secondary > .p-button:enabled:focus {
  box-shadow: 0 0 0 0.02rem var(--main-color);
}

.p-button.p-button-warning:enabled:hover,
.p-buttonset.p-button-warning > .p-button:enabled:hover,
.p-splitbutton.p-button-warning > .p-button:enabled:hover,
.mat-stroked-button:not(.mat-button-disabled) {
  border-color: var(--main-color);
}

.mat-list-item.active {
  background-color: rgba(247, 145, 60, .5);
  border-color: rgba(247, 145, 60, .5);
  border-radius: 5px;
}

.mat-list-item.active a {
  margin-left: 10px;
  transition: margin-left linear .5s;
}

mat-list-item {
  cursor: pointer;
}

mat-error {
  font-size: 15px;
}

.mat-flat-button.mat-warn,
.mat-raised-button.mat-warn,
.mat-fab.mat-warn,
.mat-mini-fab.mat-warn {
  background-color: rgba(247, 145, 60, .5);
}

.float-action-button {
  background: rgba(247, 145, 60, .8);
  width: 60px;
  height: 60px;
  border-radius: 100%;
  border: none;
  outline: none;
  font-size: 22px;
  right: 10px;
  bottom: 80px;
  position: fixed;
  transition: .3s;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.nav-pills .nav-link.active,
.nav-pills .show > .nav-link {
  background-color: rgba(247, 145, 60, .5);
}

.order-bill-title, .title {
  color: var(--main-color);
}

.nav-link {
  display: block;
  padding: 0.5rem 1rem;
  text-decoration: none;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out;
}

.mat-radio-button {
  margin-left: 10px;
}

.p-selectbutton .p-button.p-highlight {
  background: var(--main-color);
  border-color: var(--main-color);
  color: #ffffff;
}

.p-selectbutton .p-button.p-highlight:hover {
  background: rgba(247, 145, 60, .5);
  border-color: rgba(247, 145, 60, .5);
  color: #ffffff;
}

.mat-tab-group.mat-primary .mat-ink-bar, .mat-tab-nav-bar.mat-primary .mat-ink-bar {
  background-color: var(--main-color);
}

.auth-btn {
  color: #f7913c;
  background-color: #e5cbb8;
  display: block;
  margin: auto;
  width: 79%;
}

.auth-btn:hover {
  background-color: rgba(247, 145, 60, .5);
}

.submitbtn {
  background-color: #f7913c;
}

.cancelbtn {
  background-color: rgba(68, 67, 67, 0.82);
}

.dialog-title {
  color: #e19406d1;
}

.shipping-btn {
  background-color: rgba(247, 145, 60, .5);
  border: 1px solid var(--main-color);
}

.shipping-btn:hover {
  background-color: white;
  color: var(--main-color);
}

.button-select {
  background-color: var(--main-color);
  color: white !important;
}

.mat-flat-button.mat-primary, .mat-raised-button.mat-primary, .mat-fab.mat-primary, .mat-mini-fab.mat-primary {
  background-color: var(--main-color);
}

.mat-calendar-body-selected {
  background-color: var(--main-color);
  color: #fff;
}
.show-less{
  background-color: var(--main-color);
}
